<template>
  <div>
    <br />
    <table class="table table-bordered text-center">
      <tr>
        <th>BNO</th>
        <td>{{ item.no }}</td>
      </tr>
      <tr>
        <th>WRITER</th>
        <td>{{ item.writer }}</td>
      </tr>
      <tr>
        <th>TITLE</th>
        <td>{{ item.title }}</td>
      </tr>
      <tr>
        <th>REGDATE</th>
        <td>{{ getFormatDate(item.regtime) }}</td>
      </tr>
      <tr>
        <th>글 내용</th>
        <td colspan="2">
          {{ item.content }}
        </td>
      </tr>
    </table>

    <br />
    <div class="text-center">
      <button class="register-btn" @click="list">
        list
      </button>
      <button class="register-btn" @click="update">
        update
      </button>
      <button class="register-btn" @click="del">
        delete
      </button>
    </div>
    <div style="text-align: center;">
      <input
        style="width: 200px; height:30px"
        v-model="passwordcheck"
        ref="passwordcheck"
        placeholder="비밀번호를 입력해 주세요."
      />
    </div>
  </div>
</template>
<script>
import axios from 'axios';
import moment from 'moment';
export default {
  data() {
    return {
      item: {},
      passwordcheck: '',
    };
  },
  created() {
    axios
      .get(
        `http://localhost:8000/happyhouse/api/board/${this.$route.params.no}`
      )
      .then(({ data }) => {
        this.item = data;
      });
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD HH:mm:ss');
    },
    list() {
      this.$router.push('/happyhouse/');
    },
    update() {
      if (this.item.password.length == 0) {
        if (this.passwordcheck == this.item.password) {
          this.$router.push('/happyhouse/update?no=' + this.item.no);
        } else {
          alert('비밀번호를 확인해주세요');
          this.$refs.passwordcheck.focus();
        }
      } else {
        let err = true;
        let msg = '';
        !this.passwordcheck &&
          ((msg = '비밀번호를 입력해주세요'),
          (err = false),
          this.$refs.passwordcheck.focus());
        if (!err) alert(msg);
        if (this.passwordcheck.length > 0) {
          if (this.passwordcheck != this.item.password) {
            alert('비밀번호 변경 후 다시시도해주세요.');
            this.$refs.passwordcheck.focus();
            return;
          } else {
            this.$router.push('/happyhouse/update?no=' + this.item.no);
          }
        }
      }
    },
    del() {
      if (this.item.password.length == 0) {
        if (this.passwordcheck == this.item.password) {
          this.$router.push('/happyhouse/delete?no=' + this.item.no);
        } else {
          alert('비밀번호를 확인해주세요');
          this.$refs.passwordcheck.focus();
        }
      } else {
        let err = true;
        let msg = '';
        !this.passwordcheck &&
          ((msg = '비밀번호를 입력해주세요'),
          (err = false),
          this.$refs.passwordcheck.focus());
        if (!err) alert(msg);
        if (this.passwordcheck.length > 0) {
          if (this.passwordcheck != this.item.password) {
            alert('비밀번호 변경 후 다시시도해주세요.');
            this.$refs.passwordcheck.focus();
            return;
          } else {
            this.$router.push('/happyhouse/delete?no=' + this.item.no);
          }
        }
      }
    },
  },
};
</script>
<style scoped>
.table {
  margin: auto;
  width: 80%;
}

.register-btn {
  margin: 10px;
  background: white;
  border-radius: 4px;
  box-sizing: border-box;
  padding: 10px;
  letter-spacing: 1px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  min-width: 100px;
  margin-top: 8px;
  color: #8b8c8d;
  cursor: pointer;
  border: 1px solid #dddedf;
  text-transform: uppercase;
  transition: 0.1s all;
  font-size: 10px;
}
</style>
