import Vue from 'vue';
import VueRouter from 'vue-router';
import Create from '@/views/Create.vue';
import Read from '@/views/Read.vue';
import Update from '@/views/Update.vue';
import Delete from '@/views/Delete.vue';
import List from '@/views/List.vue';


Vue.use(VueRouter);

const routes = [
  { path: '/happyhouse/', name: 'list', component: List },
  { path: '/happyhouse/create', name: 'create', component: Create },
  { path: '/happyhouse/read/:no', name: 'read', component: Read },
  { path: '/happyhouse/update', name: 'update', component: Update },
  { path: '/happyhouse/delete', name: 'delete', component: Delete },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
