<template>
  <div>
    <div style="text-align: center;">
      <br />
      <input
        style="width: 300px; height:30px"
        v-model="search"
        ref="search"
        placeholder="제목을 입력해 주세요."
        @keypress.enter="searchHandler"
      />
      <button type="submit" class="btn btn-default" @click="searchHandler" >
        <i class="fa fa-search"></i>
      </button>
    </div>
    <br />
    <div v-if="items.length">
      <table class="table table-bordered text-center">
        <colgroup>
          <col width="10%" />
          <col width="50%" />
          <col width="15%" />
          <col width="25%" />
        </colgroup>
        <tr>
          <th>BNO</th>
          <th>TITLE</th>
          <th>WRITER</th>
          <th>REGDATE</th>
        </tr>
        <tr v-for="(board, index) in items" :key="index + '_items'">
          <td>{{ board.no }}</td>
          <td>
            <router-link :to="'/happyhouse/read/' + board.no">{{
              board.title
            }}</router-link>
          </td>
          <td>{{ board.writer }}</td>
          <td>{{ getFormatDate(board.regtime) }}</td>
        </tr>
      </table>
    </div>
    <div v-else class="text-center">
      게시글이 없습니다.
    </div>
  </div>
</template>
<script>
import axios from 'axios';
import moment from 'moment';

export default {
  data() {
    return {
      search:'',
      items: [],
    };
  },
  created() {
    axios.get(`http://localhost:8000/happyhouse/api/board`).then(({ data }) => {
      this.items = data;
    });
  },
  methods: {
    searchHandler() {
        this.items = [];
        axios.get(`http://localhost:8000/happyhouse/api/board`).then(({data})=>{
          let newitems = [];
          newitems = data;
          if (this.search.length == 0) {
            this.items = newitems;
            this.$refs.search.focus();
            return;
          }
          newitems.forEach((item) => {
            if(item.title.includes(this.search)){
              this.items.push(item);
            }
          });
        });
      },
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD');
    },
  },
};
</script>
<style scoped>
.table {
  margin: auto;
  width: 90%;
}
</style>
