<template>
  <div class="container">
    <div class="form-group">
      <label for="writer">작성자</label>
      <input
        type="text"
        class="form-control"
        id="writer"
        ref="writer"
        placeholder="작성자를 입력하세요"
        v-model="writer"
      />
    </div>
    <div class="form-group">
      <label for="title">제목</label>
      <input
        type="text"
        class="form-control"
        id="title"
        ref="title"
        placeholder="제목을 입력하세요"
        v-model="title"
      />
    </div>
    <div class="form-group">
      <label for="content">내용</label>
      <textarea
        type="text"
        class="form-control"
        id="contnet"
        ref="content"
        placeholder="내용을 입력하세요"
        v-model="content"
      ></textarea>
    </div>
      <div class="form-group">
      <label for="password">비밀번호</label>
      <input
        type="password"
        class="form-control"
        id="password"
        ref="password"
        placeholder="비밀번호를 입력하세요"
        v-model="password"
      />
    </div>
  
    <div class="text-center">
      <button class="register-btn" @click="checkHandler">
        create
      </button>
      <button class="register-btn" @click="moveList">
        list
      </button>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
export default {
  data() {
    return {
      no: '',
      regtime: '',
      writer: '',
      title: '',
      content: '',
      password:'',
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = '';
      !this.writer && ((msg = '작성자를 입력해주세요'), (err = false), this.$refs.writer.focus());
      err && !this.title && ((msg = '제목 입력해주세요'), (err = false), this.$refs.title.focus());
      err &&
        !this.content &&
        ((msg = '내용 입력해주세요'), (err = false), this.$refs.content.focus());
        err && !this.password && ((msg = '비밀번호를 입력해주세요'), (err = false), this.$refs.title.focus());
      if (!err) alert(msg);
      else this.createHandler();
    },
    createHandler() {
      axios
        .post(`http://localhost:8000/happyhouse/api/board`, {
          writer: this.writer,
          title: this.title,
          password :this.password,
          content: this.content,
        })
        .then(({ data }) => {
          let msg = '등록 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '등록이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        });
    },
    moveList() {
      this.$router.push('/happyhouse/');
    },
  },
};
</script>
<style></style>
