<template>
  <div>
    삭제중...
  </div>
</template>
<script>
import axios from 'axios';
export default {
  created() {
    axios.delete(`http://localhost:8000/happyhouse/api/board/${this.$route.query.no}`).then(({ data }) => {
      let msg = '삭제 처리시 문제가 발생했습니다.';
      if (data === 'success') {
        msg = '삭제이 완료되었습니다.';
      }
      alert(msg);
      this.$router.push('/happyhouse/');
    });
  },
};
</script>
<style></style>
